import axios from "axios";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const AdminEarningSummary = () => {
  const [centers, setCenters] = useState([]);
  const [loading, setLoading] = useState(false);

    const [name, setName] = useState("");
    const [type, setType] = useState("");
  // Fetch only approved centres
  const fetchApprovedCentresEarnings = async (month, year) => {
    try {
      setLoading(true);
 const res = await axios.get(
  `${process.env.REACT_APP_BACKEND_URL}/api/centres/adminEarning`, // ✅ URL (same as backend route)
  {
    params: { month, year} // ✅ params should be inside an object
  }
);

      setCenters(res.data || []);
    } catch (error) {
      console.error("Failed to fetch approved centres", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
  fetchApprovedCentresEarnings();
  }, []);



      const [currentPage, setCurrentPage] = useState(1);
  const centresPerPage = 10;

const indexOfLastCentre = currentPage * centresPerPage;
  const indexOfFirstCentre = indexOfLastCentre - centresPerPage;
  const currentCentres = centers.slice(
    indexOfFirstCentre,
    indexOfLastCentre
  );
  const totalCentresPages = Math.ceil(centers.length / centresPerPage);


  const renderPagination = () => {
    const pages = [];
    const maxVisible = 3;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalCentresPages) {
      endPage = totalCentresPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChange(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChange(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${
          currentPage === totalCentresPages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalCentresPages && handlePageChange(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

    

    return pages;
  };

    const handlePageChange = (pageNumber) => setCurrentPage(pageNumber);

      // Month list
  const months = [
    { value: "January", label: "January" },
    { value: "February", label: "February" },
    { value: "March", label: "March" },
    { value: "April", label: "April" },
    { value: "May", label: "May" },
    { value: "June", label: "June" },
    { value: "July", label: "July" },
    { value: "August", label: "August" },
    { value: "September", label: "September" },
    { value: "October", label: "October" },
    { value: "November", label: "November" },
    { value: "December", label: "December" },
  ];

    const now = new Date();
     const [selectedMonth, setSelectedMonth] = useState(months[now.getMonth()]);
  const [selectedYear, setSelectedYear] = useState(now.getFullYear());



    const handleMonthChange = (e) => {
    const month = e.target.value;
    const monthObj = months.find(m => m.value === month);
    setSelectedMonth(monthObj);
        console.log("month",monthObj.value);
        console.log("year",selectedYear);
        
        
     fetchApprovedCentresEarnings(monthObj.value, selectedYear);
  };

  const handleYearChange = (e) => {
    const year = e.target.value;
    setSelectedYear(year);
   fetchApprovedCentresEarnings(selectedMonth.value, year)
  };

//    useEffect(() => {
//     fetchEarnings();
//     fetchMonthlyEarnings();
//   }, []);

    const toTitleCase = (str = "") => {
    return str
      .toLowerCase()
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  return (
    <div className="dashboard-body__content">
      <div style={{ display: "flex", justifyContent: "flex-end" }}></div>

      <div className="card common-card border border-gray-five">
      
        <div className="card-body">
            <div
  className="d-flex align-items-center"
  style={{ justifyContent: "space-between", gap: "20px", flexWrap: "wrap" }}
>
  {/* Month & Year Filters */}
  <div className="filters d-flex" style={{ gap: "10px", alignItems: "center" }}>
    <select value={selectedMonth.value} onChange={handleMonthChange}>
      <option value="">-- Select Month --</option>
      {months.map((m) => (
        <option key={m.value} value={m.value}>
          {m.label}
        </option>
      ))}
    </select>

    <select value={selectedYear} onChange={handleYearChange}>
      {[2025].map((year) => (
        <option key={year} value={year}>
          {year}
        </option>
      ))}
    </select>
  </div>

  {/* Search & Type */}
  {/* <div className="approved-centres-header d-flex" style={{ gap: "10px", alignItems: "center" }}>
    <input
      type="text"
      className="common-input border-gray-five"
      value={name}
      onChange={(e) => setName(e.target.value)}
      placeholder="Search by Centre Name"
      style={{ flex: "1 1 200px", minWidth: "150px" }}
    />
    <select
      className="common-input border-gray-five"
      value={type}
      onChange={(e) => setType(e.target.value)}
      style={{ flex: "1 1 150px", minWidth: "150px" }}
    >
      <option value="">Select Type</option>
      <option value="Hospital">Hospital</option>
      <option value="General Clinic">General Clinic</option>
      <option value="Dental Care Centre">Dental Care Centre</option>
      <option value="Labs and Diagnostic">Labs and Diagnostic</option>
      <option value="AYUSH Centre">AYUSH Centre</option>
      <option value="Miscellaneous Healthcare Centre">
        Miscellaneous Healthcare Centre
      </option>
    </select>
  </div> */}

  {/* Selected Month & Year Display */}
  <div className="d-flex" style={{ gap: "7px", alignItems: "center" }}>
    <div>Month: {selectedMonth.label}</div>
    <div>Year: {selectedYear}</div>
  </div>
</div>

          {loading ? (
            <p>Loading...</p>
          ) : centers.length > 0 ? (
            <div className="table-responsive" style={{marginTop:"-15px"}}>
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Centre Name</th>
                    <th>Centre Type</th>
                    <th>Total Earnings</th>
                    {/* <th>Action</th> */}
                  </tr>
                </thead>
                <tbody>
                  {currentCentres.map((centre, index) => (
                    <tr key={centre._id}>
                     <td>{indexOfFirstCentre + index + 1}</td>

                      {/* <td
                        onClick={() => handleClick(centre._id)}
                        className="cursor-pointer"
                      >
                        {centre.centreName}
                      </td> */}
                      <td>
                        <Link
                          to={`/earningSumary/${centre._id}?month=${selectedMonth.value}&year=${selectedYear}`}
                          style={{ textDecoration: "underline" }}
                        >
                          {toTitleCase(centre.centreName)}
                        </Link>
                      </td>
                      <td>{centre.centreType}</td>
                      <td>{centre.totalPlatformFees || 0}</td>
                      {/* <td>
                        <button
                          style={{
                            backgroundColor: "red",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                          }}
                          onClick={() => handleDelete(centre._id)}
                        >
                          Delete
                        </button>
                      </td> */}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p className="text-muted">No approved centres found.</p>
          )}
           {currentCentres.length > 0  && totalCentresPages>1 && (
                  <nav aria-label="Service pagination" className="mt-3">
                <ul className="pagination justify-content-center">
                  {renderPagination()}
                </ul>
              </nav>
           )}
        </div>
          
      </div>
    </div>
  );
};

export default AdminEarningSummary;
